<template>
  <el-row :gutter="20">
    <el-col :span="24">
      <Panel>
        <template #title></template>
        <template #default>
          <el-row :gutter="20">
            <el-col :md="12" :offset="0">
              <LineLabel class="only-pc-display">
                <template #title></template>
                <template #default>{{ userInfo.uuid }}</template>
              </LineLabel>
              <LineLabel>
                <template #title></template>
                <template #default>{{ userInfo.userName }}</template>
              </LineLabel>
              <LineLabel>
                <template #title></template>
                <template #default>
                  <span style="color: green">{{ moreUserInfo.safe }}</span>
                </template>
              </LineLabel>
            </el-col>
            <el-col :md="12" :offset="0">
              <LineLabel>
                <template #title></template>
                <template #default>{{ userInfo.registerTime }}</template>
              </LineLabel>
              <LineLabel>
                <template #title></template>
                <template #default>{{ userInfo.loginTime }}</template>
              </LineLabel>
              <LineLabel class="only-pc-display">
                <template #title></template>
                <template #default>
                  {{ moreUserInfo.api }}
                </template>
              </LineLabel>
            </el-col>
          </el-row>
        </template>
      </Panel>
    </el-col>
  </el-row>
  <el-row :gutter="20">
    <el-col :md="16" :offset="0">
      <Panel>
        <template #title></template>
        <template #default>
          <div class="sub-title row-mt">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"> 2  18 </p>
          </div>
          <el-input
            size="small"
            placeholder=""
            v-model="userData.userName"
            :readonly="readonly.a"
            @focus="() => (readonly.a = false)"
            autocomplete="off"
          ></el-input>
          <div style="text-align: right">
            <el-button size="small" class="row-mt" @click="update(1)"></el-button>
          </div>
        </template>
      </Panel>
      <Panel>
        <template #title></template>
        <template #default>
          <div class="sub-title row-mt">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"> 6  18 </p>
          </div>
          <el-input
            size="small"
            type="password"
            v-model="userData.passWord"
            autocomplete="off"
            placeholder=""
            :readonly="readonly.b"
            @focus="() => (readonly.b = false)"
          >
          </el-input>
          <div class="sub-title row-mt">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"></p>
          </div>
          <el-input
            size="small"
            type="password"
            v-model="userData.passWord2"
            placeholder=""
            :readonly="readonly.c"
            @focus="() => (readonly.c = false)"
            autocomplete="off"
          >
          </el-input>
          <div style="text-align: right">
            <el-button size="small" class="row-mt" @click="update(2)"></el-button>
          </div>
        </template>
      </Panel>
    </el-col>
    <el-col :md="8" :offset="0">
      <Panel>
        <template #title></template>
        <template #default>
          <div class="sub-title">
            <p class="sub-title"></p>
            <p class="sub-title-info">
              
            </p>
          </div>
          <div class="sub-title">
            <p class="sub-title"></p>
            <p class="sub-title-info">
              
            </p>
          </div>
          <div class="sub-title">
            <p class="sub-title"> API </p>
            <p class="sub-title-info">
               API 
            </p>
          </div>
        </template>
      </Panel>
      <Panel>
        <template #title>API </template>
        <template #default>
          <div class="sub-title row-mt">
            <p class="sub-title-title"> API </p>
            <p class="sub-title-info">
              
            </p>
          </div>
          <div
            class="row-mt"
            style="
              padding: 12px 8px;
              background: rgba(0, 0, 0, 0.02);
              box-sizing: border-box;
              border-radius: 2px;
              font-size: 13px;
            "
          >
            <span>{{ userInfo.apiKey ? userInfo.apiKey : "" }}</span>
          </div>
          <div style="text-align: right">
            <ItemGroup>
              <el-button size="small" class="row-mt" @click="changeApi(true)"
                > API </el-button
              >
              <el-button size="small" class="row-mt" @click="changeApi(false)"
                > API </el-button
              >
            </ItemGroup>
          </div>
        </template>
      </Panel>
    </el-col>
  </el-row>
</template>

<script>
import Panel from "../../components/Panel";
import LineLabel from "../../components/LineLabel";
import { API_URL, API_USER_API, API_USER_UPDATE } from "../service/common";
import { request } from "../service/protocol";

export default {
  data() {
    return {
      readonly: {
        a: true,
        b: true,
        c: true
      },
      userData: {
        userName: "",
        passWord: "",
        passWord2: "",
        apiKey: ""
      },
      moreUserInfo: {
        api: API_URL,
        safe: ""
      },
      api: {
        enable: false,
        key: ""
      }
    };
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo;
    }
  },

  async mounted() {},
  methods: {
    async update(type) {
      await this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      });
      const userName = this.userData.userName;
      const passWord = this.userData.passWord;
      const checkPassWord = this.userData.passWord2;
      if (type === 1) {
        if (userName.length > 18 || userName < 2)
          return this.$message({
            message: "",
            type: "error"
          });
      }
      if (type === 2) {
        if (passWord.length > 18 || passWord.length < 6 || checkPassWord !== passWord)
          return this.$message({
            message: "",
            type: "error"
          });
      }
      try {
        await request({
          method: "PUT",
          url: API_USER_UPDATE,
          data: {
            userName,
            passWord
          }
        });
        this.$message({ message: "", type: "success" });
      } catch (error) {
        this.$message({
          message: "",
          type: "error"
        });
      }
    },

    async changeApi(enable) {
      try {
        const key = await request({
          method: "PUT",
          url: API_USER_API,
          data: {
            enable
          }
        });
        this.$store.commit("setApiKey", key);
        this.$message({ message: "API ", type: "success" });
      } catch (error) {
        this.$message({
          message: `:${error}`,
          type: "error"
        });
      }
    }
  },
  components: { Panel, LineLabel }
};
</script>
