<template>
  <Panel>
    <template #title></template>
    <template #default>
      <ItemGroup>
        <el-button type="success" size="small" @click="toCreate"></el-button>
        <el-button size="small" @click="refresh"></el-button>
        <el-button size="small" @click="back"></el-button>
      </ItemGroup>
    </template>
  </Panel>

  <Panel>
    <template #title></template>
    <template #default>
      <el-table :data="tasks" style="width: 100%" size="small">
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="payload" label=""></el-table-column>
        <el-table-column prop="count" label="" width="80px">
          <template #default="scope">
            <span v-if="scope.row.count > 0">{{ scope.row.count }}</span>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column prop="action" label="" width="100px">
          <template #default="scope">
            <span v-if="scope.row.action === 'command'"></span>
            <span v-if="scope.row.action === 'stop'"></span>
            <span v-if="scope.row.action === 'start'"></span>
            <span v-if="scope.row.action === 'restart'"></span>
            <span v-if="scope.row.action === 'kill'"></span>
          </template>
        </el-table-column>
        <el-table-column label="" style="text-align: center" width="120">
          <template #default="scope">
            <span v-if="scope.row.type === 1"></span>
            <span v-if="scope.row.type === 2"></span>
            <span v-if="scope.row.type === 3"></span>
          </template>
        </el-table-column>
        <el-table-column prop="time" label="" width="190px"></el-table-column>
        <el-table-column label="" style="text-align: center" width="120">
          <template #default="scope">
            <el-button type="danger" size="mini" @click="deleteTask(scope.row)">  </el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </Panel>

  <Dialog v-model="newTask.is">
    <template #title></template>
    <template #default>
      <div class="sub-title"></div>
      <el-input v-model="newTask.name" placeholder="" size="small"></el-input>
      <el-row :gutter="20">
        <el-col :span="24" :offset="0">
          <div class="sub-title row-mt">/</div>
        </el-col>
        <el-col :md="12" :offset="0">
          <el-select size="small" v-model="newTask.action" placeholder="" style="width: 100%">
            <el-option label="" value="command"></el-option>
            <el-option label="" value="start"></el-option>
            <el-option label="" value="stop"></el-option>
            <el-option label="" value="restart"></el-option>
            <el-option label="" value="kill"></el-option>
          </el-select>
        </el-col>
        <el-col :md="12" :offset="0">
          <el-select size="small" v-model="newTask.type" placeholder="" style="width: 100%">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!--  -->
      <div v-if="newTask.type == 3" class="row-mt">
        <el-date-picker
          v-model="newTask.time"
          type="datetime"
          placeholder=""
          size="small"
        ></el-date-picker>
      </div>

      <!--  -->
      <el-row :gutter="20" class="row-mt" v-if="newTask.type == 2">
        <el-col :md="24" :offset="0">
          <div class="sub-title"></div>
          <div>
            <el-time-picker
              size="small"
              v-model="newTask.objTime"
              placeholder=""
              style="width: 100%"
            ></el-time-picker>
            <el-checkbox-group v-model="newTask.weekend" class="row-mt">
              <el-checkbox size="small" label="" value="1"></el-checkbox>
              <el-checkbox size="small" label=""></el-checkbox>
              <el-checkbox size="small" label=""></el-checkbox>
              <el-checkbox size="small" label=""></el-checkbox>
              <el-checkbox size="small" label=""></el-checkbox>
              <el-checkbox size="small" label=""></el-checkbox>
              <el-checkbox size="small" label=""></el-checkbox>
            </el-checkbox-group>
          </div>
        </el-col>
        <el-col :md="6" :offset="0">
          <div class="sub-title"></div>
          <el-input
            type="number"
            v-model="newTask.count"
            placeholder=""
            size="small"
            style="width: 100%"
          ></el-input>
        </el-col>
      </el-row>

      <!--  -->
      <div v-if="newTask.type == 1" class="row-mt">
        <div class="row-mb">
          <div class="sub-title-info"></div>
        </div>
        <el-row :gutter="10">
          <el-col :md="6">
            <el-input
              size="small"
              placeholder="00"
              v-model="newTask.cycle[2]"
              style="width: 100%"
            >
              <template #append></template>
            </el-input>
          </el-col>
          <el-col :md="6">
            <el-input
              size="small"
              placeholder="00"
              v-model="newTask.cycle[1]"
              style="width: 100%"
            >
              <template #append></template>
            </el-input>
          </el-col>
          <el-col :md="6">
            <el-input
              size="small"
              placeholder="00"
              v-model="newTask.cycle[0]"
              style="width: 100%"
            >
              <template #append></template>
            </el-input>
          </el-col>
          <el-col :md="6">
            <el-input
              type="number"
              v-model="newTask.count"
              placeholder=""
              size="small"
              style="width: 100%"
            ></el-input>
          </el-col>
        </el-row>
      </div>
      <div class="sub-title row-mt"></div>
      <el-input
        v-model="newTask.payload"
        placeholder=""
        size="small"
      ></el-input>
      <div class="row-mt">
        <ItemGroup>
          <el-button type="success" size="small" @click="createTask">  </el-button>
          <el-button type="danger" size="small" @click="newTask.is = !newTask.is">  </el-button>
        </ItemGroup>
      </div>
    </template>
  </Dialog>
</template>

<script>
import Panel from "../../components/Panel";
import Dialog from "../../components/Dialog";
import { request } from "../service/protocol";
import { API_SCHEDULE } from "../service/common";
export default {
  components: { Panel, Dialog },
  data: function () {
    return {
      serviceUuid: this.$route.params.serviceUuid,
      instanceUuid: this.$route.params.instanceUuid,
      newTask: {
        is: false,
        name: "",
        action: "command",
        count: "",
        type: 1,
        payload: "",
        weekend: [],
        cycle: [0, 0, 0],
        time: "",
        objTime: null
      },
      tasks: []
    };
  },
  methods: {
    async refresh() {
      await this.render();
      this.$message({ message: "", type: "success" });
    },
    async createTask() {
      try {
        if (this.newTask.type === 1) {
          await this.createTaskType1();
        }
        if (this.newTask.type === 2) {
          await this.createTaskType2();
        }
        if (this.newTask.type === 3) {
          await this.createTaskType3();
        }
      } catch (error) {
        console.error(":", error);
      }
    },
    async render() {
      try {
        const tasks = await request({
          method: "GET",
          url: API_SCHEDULE,
          params: {
            remote_uuid: this.serviceUuid,
            uuid: this.instanceUuid
          }
        });
        for (const iterator of tasks) {
          if (iterator.type === 1) {
            const time = Number(iterator.time);
            let s = time;
            let m = 0;
            let h = 0;
            while (s >= 60) {
              s -= 60;
              m += 1;
            }
            while (m >= 60) {
              m -= 60;
              h += 1;
            }
            iterator.time = `${h}${m}${s}`;
          }
          if (iterator.type === 2) {
            const time = iterator.time;
            //16 41 15 * * 1,3,5,7,4
            const timeArr = time.split(" ");
            const h = timeArr[2];
            const m = timeArr[1];
            const s = timeArr[0];
            const w = timeArr[5];
            iterator.time = `${w} ${h}:${m}:${s}`;
          }
          if (iterator.type === 3) {
            const time = iterator.time;
            //2 3 3 27 8 *
            const timeArr = time.split(" ");
            const h = timeArr[2];
            const m = timeArr[1];
            const s = timeArr[0];
            const dd = timeArr[3];
            const mm = timeArr[4];
            iterator.time = `${mm}${dd} ${h}:${m}:${s}`;
          }
        }
        this.tasks = tasks;
      } catch (error) {
        this.$notify({
          title: "",
          message: error.toString(),
          type: "error"
        });
      }
    },
    async deleteTask(row) {
      try {
        await request({
          method: "DELETE",
          url: API_SCHEDULE,
          params: {
            remote_uuid: this.serviceUuid,
            uuid: this.instanceUuid,
            task_name: row.name
          }
        });
        this.$message({ type: "success", message: "" });
        await this.render();
      } catch (error) {
        this.$message({ type: "error", message: error.toString() });
      }
    },
    async createTaskType3() {
      const time = this.newTask.time;
      const mm = time.getMonth() + 1;
      const dd = time.getDate();
      const h = time.getHours();
      const m = time.getMinutes();
      const s = time.getSeconds();
      this.newTask.time = `${s} ${m} ${h} ${dd} ${mm} *`;
      await this.createRequest();
    },
    async createTaskType2() {
      const weekend = this.newTask.weekend;
      const w = [];
      weekend.forEach((v) => {
        if (v == "") w.push(1);
        if (v == "") w.push(2);
        if (v == "") w.push(3);
        if (v == "") w.push(4);
        if (v == "") w.push(5);
        if (v == "") w.push(6);
        if (v == "") w.push(7);
      });
      const time = this.newTask.objTime;
      const h = time.getHours();
      const m = time.getMinutes();
      const s = time.getSeconds();
      this.newTask.time = `${s} ${m} ${h} * * ${w.join(",")}`;
      await this.createRequest();
    },
    //
    async createTaskType1() {
      const arr = this.newTask.cycle;
      let ps = Number(arr[0]);
      let pm = Number(arr[1]);
      let ph = Number(arr[2]);
      const rs = ps + pm * 60 + ph * 60 * 60;
      this.newTask.time = rs;
      await this.createRequest();
    },
    async createRequest() {
      try {
        if (!this.newTask.count) this.newTask.count = -1;
        const res = await request({
          method: "POST",
          url: API_SCHEDULE,
          params: {
            remote_uuid: this.serviceUuid,
            uuid: this.instanceUuid
          },
          data: this.newTask
        });
        this.$notify({
          title: "",
          message: "",
          type: "success"
        });
        this.newTask = {
          is: false,
          name: "",
          action: "command",
          count: 1,
          type: 1,
          payload: "",
          weekend: [],
          cycle: [0, 0, 0],
          time: ""
        };
        return res;
      } catch (error) {
        this.$message({ type: "error", message: error.toString() });
      }
    },
    back() {
      this.$router.go(-1);
    },
    toCreate() {
      this.newTask.is = true;
    }
  },
  async mounted() {
    await this.render();
  }
};
</script>
