<template>
  <Panel style="max-width: 900px">
    <template #title>{{ title }}</template>
    <template #default>
      <div v-show="page == 0" class="panel-context row-mt">
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-row :gutter="10" justify="center" class="col-md-responsive">
          <el-col :md="8" :offset="0">
            <SelectBlock @click="selectTypeA(1)" style="min-height: 120px">
              <template #title>Java  Minecraft </template>
              <template #info>  SpigotBungeecord  Jar  </template>
            </SelectBlock>
          </el-col>
          <el-col :md="8" :offset="0">
            <SelectBlock @click="selectTypeA(2)" style="min-height: 120px">
              <template #title> Minecraft </template>
              <template #info>
                 Bedrock Dedicated Server 
              </template>
            </SelectBlock>
          </el-col>
          <el-col :md="8" :offset="0">
            <SelectBlock @click="selectTypeA(3)" style="min-height: 120px">
              <template #title></template>
              <template #info>  bashcmd.exe  </template>
            </SelectBlock>
          </el-col>
        </el-row>
      </div>

      <div v-show="page == 1" class="panel-context row-mt">
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info">
            
          </p>
        </div>

        <el-row :gutter="10" justify="center" class="col-md-responsive">
          <el-col :md="8" :offset="0">
            <SelectBlock @click="selectTypeB(1)" style="min-height: 120px">
              <template #title> ()</template>
              <template #info>
                
              </template>
            </SelectBlock>
          </el-col>
          <el-col :md="8" :offset="0">
            <SelectBlock @click="selectTypeB(2)" style="min-height: 120px">
              <template #title></template>
              <template #info>
                 zip 
              </template>
            </SelectBlock>
          </el-col>
          <el-col :md="8" :offset="0">
            <SelectBlock @click="selectTypeB(3)" style="min-height: 120px">
              <template #title>/</template>
              <template #info>
                
              </template>
            </SelectBlock>
          </el-col>
        </el-row>
        <div class="row-mt" style="text-align: center">
          <el-button @click="up" size="small"></el-button>
        </div>
      </div>

      <!--  -->
      <div v-show="page == 2 && typeB == 1" class="panel-context row-mt">
        <div class="">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"></p>
          </div>
          <el-input
            placeholder=""
            v-model="form.nickname"
            :disabled="assist.creating"
          >
          </el-input>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p
              class="sub-title-info"
              v-text="' {{ProgramName}} '"
            ></p>
          </div>
          <div class="flex">
            <el-input
              v-model="assist.commandtemplate"
              placeholder=" java -jar server.jar"
              :disabled="assist.creating"
            ></el-input>
            <el-button @click="openCommandAssistCall(1)"></el-button>
          </div>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info">
              : C:/Servers/MyServer
            </p>
          </div>
          <el-input placeholder="" v-model="form.cwd" :disabled="assist.creating">
          </el-input>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info">
               JAR/EXE  Spigot.jarPaper.jar 
            </p>
          </div>
          <el-button @click="uploadFile(1)" :disabled="assist.creating"></el-button>
          <div v-if="percentComplete > 0">
            <el-progress :percentage="percentComplete"></el-progress>
          </div>
          <p></p>
        </div>
        <div class="row-mt" style="text-align: center">
          <el-button @click="up" size="small" :disabled="assist.creating"></el-button>
          <!-- <el-button @click="down" size="small"></el-button> -->
        </div>
      </div>

      <!--  -->
      <div v-show="page == 2 && typeB == 2" class="panel-context row-mt">
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"></p>
          </div>
          <el-input
            placeholder=""
            v-model="form.nickname"
            :disabled="assist.creating"
          >
          </el-input>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"></p>
          </div>
          <div class="flex">
            <el-input
              v-model="form.startCommand"
              placeholder=" java -jar server.jar"
              :disabled="assist.creating"
            ></el-input>
            <el-button @click="openCommandAssistCall(2)"></el-button>
          </div>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info">
              : C:/Servers/MyServer
            </p>
          </div>
          <el-input placeholder="" v-model="form.cwd" :disabled="assist.creating">
          </el-input>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"> ZIP  </p>
          </div>
          <el-button @click="uploadFile(2)" :disabled="assist.creating"></el-button>
          <div v-if="percentComplete > 0">
            <el-progress :percentage="percentComplete"></el-progress>
          </div>
          <p></p>
        </div>
        <div class="row-mt" style="text-align: center">
          <el-button @click="up" size="small" :disabled="assist.creating"></el-button>
          <!-- <el-button @click="createInstance" size="small"></el-button> -->
        </div>
      </div>

      <!--  -->
      <div v-show="page == 2 && typeB == 3" class="panel-context row-mt">
        <div>
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"></p>
          </div>
          <el-input placeholder="" v-model="form.nickname"> </el-input>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info"></p>
          </div>
          <div class="flex">
            <el-input
              v-model="form.startCommand"
              placeholder=" java -jar server.jarcmd.exe "
            ></el-input>
            <el-button @click="openCommandAssistCall(2)"></el-button>
          </div>
        </div>
        <div class="row-mt">
          <div class="sub-title">
            <p class="sub-title-title"></p>
            <p class="sub-title-info">
              : C:/Servers/MyServer
            </p>
          </div>
          <el-input
            placeholder=": C:/Servers/MyServer"
            v-model="form.cwd"
          >
          </el-input>
        </div>
        <p></p>
        <div class="row-mt" style="text-align: center">
          <ItemGroup>
            <el-button @click="up" size="small"></el-button>
            <el-button @click="createInstance" size="small"></el-button></ItemGroup
          >
        </div>
      </div>

      <div v-show="page == 3" class="panel-context row-mt">
        <div class="sub-title">
          <p class="sub-title-title"></p>
          <p class="sub-title-info"></p>
        </div>
        <el-row :gutter="10" justify="center" class="col-md-responsive">
          <el-col :md="8" :offset="0">
            <SelectBlock @click="toEdit" style="min-height: 120px">
              <template #title></template>
              <template #info>
                 Docker 
              </template>
            </SelectBlock>
          </el-col>
        </el-row>
      </div>
    </template>
  </Panel>

  <!--  -->
  <CommandAssist
    v-model="commandAssistPanel"
    :result="commandAssistCallback"
    :default-program-name="assist.defaultProgramName"
  >
  </CommandAssist>

  <!--  -->
  <input type="file" ref="fileButtonHidden" @change="selectedFile" hidden="hidden" />
</template>

<script>
import path from "path";
import axios from "axios";
import Panel from "../../components/Panel";
import CommandAssist from "../../components/CommandAssist";

import SelectBlock from "../../components/SelectBlock";
import { API_INSTANCE, API_INSTANCE_UPLOAD } from "../service/common";
import {
  TYPE_MINECRAFT_JAVA,
  TYPE_MINECRAFT_BEDROCK,
  TYPE_UNIVERSAL
} from "../service/instance_type";
import { parseforwardAddress, request } from "../service/protocol";

export default {
  components: { Panel, SelectBlock, CommandAssist },
  data: function () {
    return {
      title: "",
      page: 0,
      typeA: -1,
      typeB: -1,
      commandAssistPanel: false,
      serviceUuid: this.$route.params.serviceUuid,
      form: {
        nickname: "",
        startCommand: "",
        stopCommand: "^c",
        cwd: "",
        ie: "GBK",
        oe: "GBK",
        createDatetime: new Date().toDateString(),
        lastDatetime: "",
        type: TYPE_UNIVERSAL,
        tag: [],
        maxSpace: null,
        endTime: ""
      },
      assist: {
        defaultProgramName: "",
        commandtemplate: "",
        uploadFileType: 0,
        creating: false
      },
      newInstanceUuid: "",
      uploadConfig: {
        addr: "",
        password: ""
      },
      percentComplete: 0
    };
  },
  methods: {
    // 
    async uploadFile(type) {
      if (!this.form.nickname || (!this.assist.commandtemplate && !this.form.startCommand)) {
        return this.$message({ message: "", type: "error" });
      }
      await this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      });
      this.assist.uploadFileType = type;
      this.$refs.fileButtonHidden.click();
    },
    // 
    async selectedFile() {
      try {
        this.assist.creating = true;
        const file = this.$refs.fileButtonHidden.files[0];
        // 
        if (this.assist.uploadFileType === 1) {
          this.form.startCommand = this.assist.commandtemplate.replace(
            "{{ProgramName}}",
            file.name
          );
        }
        if (!this.form.cwd) this.form.cwd = ".";
        //  & 
        const cfg = await request({
          method: "POST",
          url: API_INSTANCE_UPLOAD,
          params: {
            upload_dir: ".",
            remote_uuid: this.serviceUuid
          },
          data: this.form
        });
        this.uploadConfig.addr = parseforwardAddress(cfg.addr, "http");
        this.uploadConfig.password = cfg.password;
        this.newInstanceUuid = cfg.instanceUuid;
        // 
        const formData = new FormData();
        formData.append("file", file);
        const fullAddress = `${this.uploadConfig.addr}/upload/${this.uploadConfig.password}`;
        console.log(" - :", fullAddress, "\n", file);
        // 
        const fileName = file.name;
        const extName = path.extname(fileName);
        await axios.post(fullAddress, formData, {
          params: {
            // 
            unzip: extName === ".zip" ? 1 : null
          },
          headers: { "Content-Type": "multipart/form-data" },
          onUploadProgress: (progressEvent) => {
            this.percentComplete = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          }
        });
        this.$message({ message: ``, type: "success" });
        this.down();
        this.title = "";
      } catch (error) {
        this.$message({ message: `:${error.message}`, type: "error" });
      }
    },
    // 
    async createInstance() {
      await this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      });
      if (this.form.nickname === "" || this.form.startCommand === "") {
        return this.$message({ message: "", type: "error" });
      }
      try {
        if (!this.form.cwd) this.form.cwd = ".";
        const data = await request({
          method: "POST",
          url: API_INSTANCE,
          params: { remote_uuid: this.serviceUuid },
          data: this.form
        });
        this.$message({ message: "", type: "success" });
        this.newInstanceUuid = data.instanceUuid;
        this.down();
      } catch (err) {
        this.$message({
          type: "error",
          message: `: ${err.message}`
        });
        console.error(":", err);
      }
    },
    selectTypeA(v) {
      if (v === 1) {
        this.form.type = TYPE_MINECRAFT_JAVA;
        this.assist.commandtemplate = "java -jar {{ProgramName}}";
        this.form.stopCommand = "stop";
      }
      if (v === 2) {
        this.form.type = TYPE_MINECRAFT_BEDROCK;
        this.assist.commandtemplate = "{{ProgramName}}";
        this.form.stopCommand = "stop";
      }
      if (v === 3) {
        this.form.type = TYPE_UNIVERSAL;
        this.assist.commandtemplate = "{{ProgramName}}";
        this.form.stopCommand = "^c";
      }
      this.title = "";
      this.typeA = v;
      this.down();
    },
    selectTypeB(v) {
      this.title = "/";
      this.typeB = v;
      this.down();
    },
    toEdit() {
      this.$router.push({ path: `/instance_detail/${this.serviceUuid}/${this.newInstanceUuid}/` });
    },
    up() {
      this.title = "";
      if (this.page > 0) this.page -= 1;
    },
    down() {
      this.page += 1;
    },
    openCommandAssistCall(type) {
      if (type === 1) {
        this.assist.defaultProgramName = "{{ProgramName}}";
      } else {
        this.assist.defaultProgramName = "";
      }
      this.commandAssistPanel = true;
    },
    commandAssistCallback(cmd) {
      this.assist.commandtemplate = cmd;
      this.form.startCommand = cmd;
    }
  },
  async mounted() {}
};
</script>
